import 'ScssComponents/step-form-list.scss';
import 'ScssComponents/step-form-head.scss';
import quizItem from './quiz-item';
import { exist } from '../utils';

const onboarding2 = () => {
	quizItem();

	const selectors = {
		quizInput: '.js-quiz-input',
		onboarding: '.js-onboarding',
	};

	const classnames = {
		onboardingActiveState: 'onboarding_2--active_state',
	};

	const $inputCheck = document.querySelectorAll(selectors.quizInput);
	if (!exist($inputCheck)) return;

	const $onboarding = document.querySelector(selectors.onboarding);

	$inputCheck.forEach(($input) => {
		document.addEventListener('click', (e) => {
			const targetElement = e.target;

			if (targetElement.closest(selectors.quizInput) && $input.checked) {
				$onboarding.classList.add(classnames.onboardingActiveState);
			}
		});
	});
};

export default onboarding2;
